import React from 'react';
import axios from 'axios';
import './App.css';
import Pad from './Pad';
import {ReactComponent as Pencil} from './icons/pencil.svg'
import {ReactComponent as Eraser} from './icons/eraser.svg'
import {ReactComponent as Predict} from './icons/prediction.svg'

function App() {
  let i;
  let [cords,cordsState]=React.useState([]);
  let cell = 28;
  let cells = cell * cell;
  let [val,valState] = React.useState("Output");

  const draw = (cord) =>{
    if(window.mouse){
        cords[cord] = 1;
        cords[cord-cell] = 1;
        cords[cord+cell] = 1;
        cords[cord-1] = 1;
        cords[cord+1] = 1;
        cordsState(cords)
        gridState(
        <Pad grid={cords} draw={draw} />  
      )
    }
  }

  const clearPad = () =>{
    for (i = 0;i<cells;i++){
      cords[i] = 0;
    }
    cordsState(cords)
    gridState(
      <Pad grid={cords} draw={draw} />
    )
    valState("Output")
  }

  let [grid,gridState] = React.useState(
    <Pad grid={[]} draw={draw} />
  )

  React.useState(()=>{
    for (i = 0;i<cells;i++){
      cords.push(0);
    }
    cordsState(cords)
    gridState(
      <Pad grid={cords} draw={draw} />
    )
  },[]) 

  const predict = () =>{
    async function fetch(){
      await axios({
        url:"/predict",
        method:"POST",
        data:{
          image:cords.slice(0,784)
        }
      }).then(response=>{
        console.log(response.data)
        if (response.data.status){
          valState(response.data.digit)
        }
      })
    }
    fetch()
  }

  return (
    <section className="flex-center">
      <div className="padContainer">
        <div className="title flex-center">
          MNIST Pad
        </div>

        <div className="flex-col">
          <div className="pad">
            {grid}
            <div className="tools">
              <div className="tool flex-center">
                <Pencil />
              </div>
              <div className="tool flex-center" onClick={clearPad}>
                <Eraser />
              </div>
              <div className="tool flex-center" onClick={predict}>
                <Predict />
              </div>
              <div className="output flex-center">
                {val}
              </div>
            </div>
          </div>
        </div> 

        <div className="footer flex-center">
          Viraj Patel
        </div>
      </div>
    </section>
  );
}

export default App;
