import React from 'react';

const Pad = (props) =>{

    let cells = props.grid.map((val,i)=>{
        return (
            <div 
                className={"cell-"+val} 
                onMouseOver={()=>{
                    props.draw(i);
                }} 
                key={i}
            >
                {""}
            </div>
        )
    })

    return  (
        <div className="grid">
            {cells}
        </div>
    )
}

export default Pad;